/* styles.css */

.container {
  display: grid;
  grid-template-columns: 1.1fr 2fr 0.8fr; /* 화면을 두 개의 열로 나눕니다. */
  height: 100vh; /* 화면 전체 높이를 사용합니다. */
}

.block {
  text-align: center;
}



.block1 {
  /* 각 블록의 스타일 설정 */
  background-color: #f3c1c1;
  margin-left: 20px;
  margin-right: 20px;
}

.block1 p {
  font-weight: bold;
  font-size: 150%;
}

.block2 {
  /* 각 블록의 스타일 설정 */
  background-color: #dcf5d1;
}

.block2 p {
  font-weight: bold;
  font-size: 150%;

}

.block3 {
  /* 각 블록의 스타일 설정 */
  background-color: #fbfcc2;
}

.block3 p {
  font-weight: bold;
  font-size: 150%;

}